interface paramsProps {
  [key: string]: string;
}
const getUrlParams = (url = window.location.href): paramsProps => {
  const params = {} as paramsProps;

  if (!url || typeof url !== 'string' || url.lastIndexOf('?') === -1)
    return params;

  const hashIndex = url.indexOf('#');
  let str = url;
  if (hashIndex > 0) {
    // 为兼容 hash history 模式
    str = url.slice(0, hashIndex);
    str.lastIndexOf('?') === -1 && (str = url.slice(hashIndex, url.length));
  }

  const search = str.slice(str.lastIndexOf('?') + 1);
  let arr = search.split('&');

  while (arr.length) {
    const value = arr.pop() || '';
    const [k, v] = value.split('=');
    k && (params[k] = v ? decodeURIComponent(v) : v);
  }

  arr = [];

  return params;
};

export default getUrlParams;
