import React from 'react';
import styled from 'styled-px2vw';
import { Result } from 'antd-mobile';
import imgErrorIcon from './error-icon.svg';

type Error = {
  message: string;
  stack: string;
};

type StateType = {
  hasError: boolean;
  errMsg: string;
  errStack: string;
};

const ErrorContainer = styled.div`
  .error-icon-spe {
    width: 120px;
    height: 120px;
  }
`;

class ErrorBoundary extends React.Component<unknown, StateType> {
  constructor(props: unknown) {
    super(props);
    this.state = {
      hasError: false,
      errMsg: '',
      errStack: '',
    };
  }

  static getDerivedStateFromError(error: Error): StateType {
    return {
      hasError: true,
      errMsg: error.message,
      errStack: error.stack,
    };
  }

  render(): React.ReactNode {
    const { hasError, errMsg, errStack } = this.state;
    const { children } = this.props;
    if (hasError) {
      return (
        <ErrorContainer>
          <Result
            img={<img src={imgErrorIcon} className="error-icon-spe" alt="" />}
            title="出现了一些问题"
            message={
              <div>
                <p>可能是网络原因导致的，请刷新页面或稍后再试</p>
                <p>
                  <small
                    style={{
                      fontSize: '0.8em',
                    }}
                  >
                    错误代码：{errMsg}
                  </small>
                </p>
              </div>
            }
            buttonText="刷新"
            onButtonClick={(): void => {
              window.location.reload();
            }}
          />
          <pre
            style={{
              display: 'none',
            }}
          >
            {errStack}
          </pre>
        </ErrorContainer>
      );
    }

    return children;
  }
}

export default ErrorBoundary;
