/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
/* eslint-disable @typescript-eslint/no-explicit-any */
import React from 'react';
import { Route } from 'react-router-dom';
import getUrlParams from '@/utils/get-url-params';
import refreshTitle from '@/utils/refreshTitle';

const RouterProxy = ({ component: Component, ...rest }: any): any => {
  const title = getUrlParams().title || rest.title;
  title && refreshTitle(decodeURIComponent(title));

  return (
    <Route
      {...rest}
      render={(props): React.ReactNode => <Component {...props} />}
    />
  );
};

export default RouterProxy;
